import { Component, OnInit, ViewEncapsulation, AfterViewInit } from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';


@Component({
    selector: "app-index",
    templateUrl: "./index.component.html",
    encapsulation: ViewEncapsulation.None,
})
export class IndexComponent implements OnInit, AfterViewInit {


    constructor(private _script: ScriptLoaderService) {

    }
    ngOnInit() {

    }
    ngAfterViewInit() {
        this._script.loadScripts('app-index',
            ['assets/app/js/dashboard.js']);
        let datatable = (<any>$('#m_datatable_latest_orders')).mDatatable({
            data: {
                type: "remote",
                source: {
                    read: {
                        url: "/api/TestApi/Index"
                    }
                },
                pageSize: 10,
                saveState: {
                    cookie: !1,
                    webstorage: !0
                },
                serverPaging: !0,
                serverFiltering: !0,
                serverSorting: !0
            },
            layout: {
                theme: "default",
                class: "",
                scroll: !0,
                height: 380,
                footer: !1
            },
            sortable: !0,
            filterable: !1,
            pagination: !0,
            columns: [{
                field: "RecordID",
                title: "#",
                sortable: !1,
                width: 40,
                selector: {
                    class: "m-checkbox--solid m-checkbox--brand"
                },
                textAlign: "center"
            }, {
                field: "time",
                title: "Thời gian",
                sortable: "asc",
                filterable: !1,
                width: 150
            }, {
                field: "type",
                title: "Kiểu",
                width: 100,
                responsive: {
                    visible: "lg"
                }
            }, {
                field: "from",
                title: "Từ"
            },
            {
                field: "to",
                title: "Tới",
                width: 100
            }, {
                field: "detail",
                title: "Nội dung",
                width: 200
            }]
        });
    }

}


